.projects-container {
    /* inherit page bg rather than default grey */
    background: inherit;
  
    &.light {
      background: #f8f9fa;    /* match your light‐mode body */
    }
    &.dark {
      background: #0d1117;    /* match your dark‐mode body */
    }
  
    padding: 5% 10%;
    color: inherit;
  
    h1 {
      margin-bottom: 1rem;
    }
  }
  
  .projects-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 50px;
  
    @media (max-width: 700px) {
      display: block;
    }
  }
  
  /* style each project exactly like your Experience cards */
  .project {
    border-radius: 12px;
    overflow: hidden;
    cursor: pointer;
    transition: transform .2s ease, box-shadow .2s ease, outline .2s ease;
  
    /* panel background & shadow */
    .projects-container.light & {
      background: #ffffff;
      box-shadow: 0 5px 10px rgba(0,0,0,0.2);
    }
    .projects-container.dark & {
      background: #1e1f23;
      box-shadow: 0 5px 10px rgba(255,255,255,0.1);
    }
  
    &:hover {
      transform: translateY(-4px);
  
      /* mode-aware outline */
      /* outline-offset pulls it just outside the rounded edge */
      outline-offset: 2px;
  
      /* light mode = black border, dark = white */
      .projects-container.light & {
        outline: 2px solid #000;
      }
      .projects-container.dark & {
        outline: 2px solid #fff;
      }
    }
  
    img {
      width: 100%;
      display: block;
      object-fit: cover;
    }
  
    h2 {
      margin: 1rem;
    }
    p {
      margin: 0 1rem 1rem;
    }
  
    @media (max-width: 700px) {
      margin-bottom: 2rem;
    }
  }
  
  /* remove the little zoom‐on‐hover */
  .zoom {
    border-radius: 5px;
    transition: none !important;
    margin: 0 auto;
  
    &:hover {
      transform: none !important;
    }
  }
  
  /* full‐screen modal overlay (unchanged) */
  .project-modal {
    position: fixed;
    inset: 0;
    z-index: 2000;
    display: flex;
    align-items: center;
    justify-content: center;
  
    .modal-backdrop {
      position: absolute;
      inset: 0;
      background: rgba(0,0,0,0.6);
    }
  
    .modal-card {
        position: relative;
        border-radius: 12px;
        padding: 2rem;
    
        /* ↑— here’s the corrected size caps: */
        max-width: 900px;    /* up from 600px */
        width: 85%;          /* responsive fallback */
        max-height: 100vh;    /* cap at 90% of the viewport height */
        overflow-y: auto;    /* scroll inside if content is too tall */
    
        box-shadow: 0 10px 20px rgba(0,0,0,0.5);
        color: inherit;
        transition: background .3s, color .3s;
    
        &.light {
          background: #ffffff;
          color: #000;
        }
        &.dark {
          background: #1e1f23;
          color: #fff;
        }
    
        img {
          display: block;
          width: 100%;
          height: 250px;      /* make the image taller if you like */
          object-fit: contain;
          border-radius: 8px;
          margin-bottom: 1rem;
        }
    
        h2 { margin-top: 0; }
        p  { margin: 0 0 1rem; }
    
        a {
          display: inline-block;
          margin-top: 0.5rem;
          color: #00d2ff;
          text-decoration: underline;
        }
    
        .modal-close {
          position: absolute;
          top: 0.5rem;
          right: 0.75rem;
          background: transparent;
          border: none;
          font-size: 2rem;
          color: inherit;
          cursor: pointer;
        }
    }
  }